/** @jsx jsx */
import jsx from '../jsx'
import React from 'react'
import { sample } from 'lodash'
import chroma from 'chroma-js'
import theme from '../theme'
import data from '../data/content'


import Logo from '../components/Logo'
import Card from '../components/Card'
import Container from '../components/Container'
import SiteHeader from '../components/SiteHeader'

const getRandomInt = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

const hues = [
  theme.colors.cyan,
  theme.colors.blue,
  theme.colors.indigo,
  theme.colors.violet,
  theme.colors.red,
  theme.colors.gray,
  theme.colors.yellow,
]

const hue = sample(hues)

const colorIndex = getRandomInt(0,10)

const colorPrimary = hue[colorIndex]
const colorPrimaryAlt = chroma.contrast(colorPrimary, 'white') > 4.49? hue[0]: hue[10]

// Typography
//


const navThemeInt = getRandomInt(0,9)
const SectionTitleVariant = getRandomInt(1,2)
const HorizontalPadding = getRandomInt(4,4)


const serif =['athelas, serif', 
      'palatino, serif',
      'garamond, serif', 
      'georgia, serif', 
      'cambria, serif', 
      'calibri, serif', 
      'baskerville, serif', 
]

const sansSerif = [      'verdana, sans-serif',
      'avenir, sans-serif',
      'sitka, sans-serif',
      'seavek, sans-serif',
      'helvetica, sans-serif', 
]

const mono = [
      'monaco, monospace',
      'consolas, monospace',
      'courier, monospace'
]

const fontFamilyInt = getRandomInt(0,9)

let fontFamily = ''
if (fontFamilyInt < 4) { 
  fontFamily = sample(serif)
} else if (fontFamilyInt < 7) {
  fontFamily = sample(sansSerif)
} else {
  fontFamily = sample(mono)
}

function Home() {
  return (
    <div 
      bg={colorPrimary} 
      color={colorPrimaryAlt} 
    fontFamily={fontFamily}
    >
    <SiteHeader px={[3,4,5]} 
      bg={navThemeInt > 4? colorPrimaryAlt: colorPrimary} 
      color={navThemeInt > 4? colorPrimary: colorPrimaryAlt}  
    />
    <div px={[3,4,5]} pt={4}>
    <header mb={[5,6]}>
      <h1 mt={0} fontSize={1} textStyle={sample(['caps', 'none'])}>{data.title}</h1>
      <h2 my={0} fontSize={[2,2,getRandomInt(3,6)]} maxWidth='16em' textStyle={sample(['caps', 'none'])}>{data.description}</h2>
    </header>

    <div display='flex' flexWrap={['wrap', 'wrap','nowrap']} mx={-4} pb={5}>
      <div display='flex' flexWrap={['wrap']} width={[1,1,1/3]} px={4}>
        {data.projects.map((item, i) => (
          <Card 
            href={item.href} 
            title={item.title} 
            image={item.image} 
            text={item.text}
            width={[1]}
            variant={3}
          />
            
        ))}
      </div>
      <div width={[1, 1/2, 1/3]} px={4}>
        <h3 mt={[3,0,0]} variant={SectionTitleVariant}>Writing</h3>
        {data.writingLinks.map((link, i) => (
          <a display='block' py={1} href={link.href}>{link.name}</a> 
        ))}
      </div>
      <div width={[1,1/2, 1/3]} px={4}>
        <h3 mt={[3,0,0]} variant={SectionTitleVariant}>Reading</h3>
        {data.readingLinks.map((link, i) => (
          <a display='block' py={1} href={link.href}>{link.name}</a> 
        ))}
      </div>
    </div>
    </div>
    <footer px={[3,4,5]} display='flex' alignItems='center' py={4} borderTop='1px solid rgba(0,0,0,.2)'>
        {data.socialLinks.map((link, i) => (
          <a href={link.href} mr={3}>{link.title}</a>
        ))}
    </footer>
    </div>
  );
}

export default Home
