import React from "react"
import { sample } from 'lodash'
import theme from "../theme"

const getRandomInt = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

const hues = [
  theme.colors.cyan,
  theme.colors.blue,
  theme.colors.indigo,
  theme.colors.violet,
  theme.colors.red,
  theme.colors.gray,
  theme.colors.yellow,
]

const hue = sample(hues)

const colorIndex = getRandomInt(0,10)

const colorPrimary = hue[colorIndex]
const colorInt = getRandomInt(0,9)
const opacityInt = getRandomInt(0,9)

