/** @jsx jsx */
import jsx from '../jsx'
import React from 'react'
import {sample } from 'lodash'
import Logo from './Logo'
import NavLink from './NavLink'
import data from '../data/content'

const getRandomInt = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

const padding = getRandomInt(2,5)

const SiteHeader = ({variant,  ...props}) => {
  switch (variant) {
    case 1:
      return (
        <div display='flex' alignItems='center' py={padding} mb={padding} borderBottom={sample(['1px solid rgba(0,0,0,.15)', 'none', 'none', 'none'])} {...props}>
          <Logo maxWidth='4em' width={sample(['2em', '3em'])} 
            style={{
              mixBlendMode: sample([
                'none',
                'none',
                'none',
                'overlay',
                'difference',
              ])
            }}
        />
          <nav display='flex' ml='auto'>
            {data.navLinks.map((item, i) => (
              <NavLink display='block' href={item.href} ml={[3,4]}>
                {item.title}
              </NavLink>
            ))}
          </nav>
        </div>
      )
    case 2:
      return (
        <div />
      )

    // You can have as many cases as you want!

  }
}


SiteHeader.defaultProps = {
  variant: 1
}

export default SiteHeader
