/** @jsx jsx */
import jsx from '../jsx'
import React from 'react'
import { sample } from 'lodash'

import BlockLink from './BlockLink'

const getRandomInt = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

const fontWeight = sample([400,500,600,700,800,900])
const fontSize = sample([0,1])
const textStyle = sample(['caps', 'none'])

const NavLink = ({variant,  ...props}) => {
  switch (variant) {
    case 1:
      return (
        <BlockLink
            fontSize={fontSize}
            fontWeight={fontWeight}
            textStyle={textStyle}
            {...props}
          >
          {props.children}
        </BlockLink>
      )
    case 2:
      return (
        <BlockLink>{props.children}</BlockLink>
      )

    // You can have as many cases as you want!

  }
}


NavLink.defaultProps = {
  variant: 1,
  color: 'inherit'
}

export default NavLink
